import { useEffect, useRef, useState } from "react";
import { useLocation } from "react-router-dom";
import {
  Send,
  Mic,
  Image as ImageIcon,
  Check,
  CheckCheck,
  CircleDot,
  Play,
  Pause
} from "lucide-react";
import { createClient } from "@supabase/supabase-js";

// --------------------------------------
// BACKEND + STORAGE CONFIG
// --------------------------------------
const API_URL = "http://localhost:4000";

const SUPABASE_URL = "https://pfbqqrnovujmeadowefl.supabase.co";
const SUPABASE_ANON_KEY =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InBmYnFxcm5vdnVqbWVhZG93ZWZsIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NjQ5MjQyMzgsImV4cCI6MjA4MDUwMDIzOH0.Qpqv9ebM-ikfdHX1dT7ZtHTUPr-AxBDXDwGlZUsvYUE";

const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY);

// --------------------------------------
// HELPER: Get logged in user
// --------------------------------------
function getCurrentUser() {
  try {
    const raw = localStorage.getItem("currentUser");
    if (!raw) return null;
    return JSON.parse(raw);
  } catch {
    return null;
  }
}

const currentUser = getCurrentUser();
const currentUserId = currentUser?.id || "guest-user";

// Message status
type MessageStatus = "sent" | "delivered" | "seen";

// --------------------------------------
// COMPONENT
// --------------------------------------
export default function MessagesScreen() {
  const location = useLocation();
  const params = new URLSearchParams(location.search);

  const matchId = params.get("matchId");
  const otherUserId = params.get("other");

  const [messages, setMessages] = useState<any[]>([]);
  const [text, setText] = useState("");

  const [isTyping, setIsTyping] = useState(false);
  const [otherTyping, setOtherTyping] = useState(false);
  const [isOnline, setIsOnline] = useState(true);

  const [isRecording, setIsRecording] = useState(false);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);

  const bottomRef = useRef<HTMLDivElement | null>(null);
  const typingTimeoutRef = useRef<number | null>(null);

  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [audioPlayingId, setAudioPlayingId] = useState<string | null>(null);

  // Scroll down
  const scrollToBottom = () => bottomRef.current?.scrollIntoView({ behavior: "smooth" });

  const normalizeStatus = (m: any): MessageStatus =>
    m.seen ? "seen" : m.delivered ? "delivered" : "sent";

  // --------------------------------------
  // LOAD MESSAGES + MARK delivered/seen
  // --------------------------------------
  const loadMessages = async () => {
    if (!matchId) return;

    try {
      const res = await fetch(`${API_URL}/messages/${matchId}`);
      const json = await res.json();

      if (!json.success) return;

      const data = json.data as any[];

      const withStatus = data.map((m) => ({
        ...m,
        _status: normalizeStatus(m),
      }));

      setMessages(withStatus);
      scrollToBottom();

      const received = data.filter((m) => m.receiver_id === currentUserId);

      const needDelivered = received.filter((m) => !m.delivered).map((m) => m.id);
      if (needDelivered.length > 0) {
        await fetch(`${API_URL}/message-delivered`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ ids: needDelivered }),
        });
      }

      const needSeen = received.filter((m) => !m.seen).map((m) => m.id);
      if (needSeen.length > 0) {
        await fetch(`${API_URL}/message-seen`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ ids: needSeen }),
        });
      }
    } catch (err) {
      console.error("load error", err);
    }
  };

  useEffect(() => {
    loadMessages();
    const int = window.setInterval(loadMessages, 1500);
    return () => clearInterval(int);
  }, [matchId]);

  // --------------------------------------
  // Input typing indicator
  // --------------------------------------
  const handleInputChange = (value: string) => {
    setText(value);
    if (!isTyping) setIsTyping(true);

    if (typingTimeoutRef.current) clearTimeout(typingTimeoutRef.current);
    typingTimeoutRef.current = window.setTimeout(() => setIsTyping(false), 1200);
  };

  const triggerOtherTyping = () => {
    setOtherTyping(true);
    setTimeout(() => setOtherTyping(false), 2000);
  };

  // --------------------------------------
  // SEND MESSAGE TO BACKEND
  // --------------------------------------
  const sendMessageToBackend = async (payload: any) => {
    const res = await fetch(`${API_URL}/send-message`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });
    return await res.json();
  };

  // --------------------------------------
  // SEND TEXT MESSAGE
  // --------------------------------------
  const sendTextMessage = async () => {
    if (!text.trim()) return;

    const content = text;
    const tempId = `local-${Date.now()}`;

    const newMsg = {
      id: tempId,
      sender_id: currentUserId,
      receiver_id: otherUserId,
      match_id: matchId,
      type: "text",
      message: content,
      created_at: new Date().toISOString(),
      delivered: false,
      seen: false,
      _status: "sent" as MessageStatus,
    };

    setMessages((prev) => [...prev, newMsg]);
    scrollToBottom();
    setText("");

    triggerOtherTyping();

    const json = await sendMessageToBackend({
      match_id: matchId,
      sender_id: currentUserId,
      receiver_id: otherUserId,
      message: content,
      type: "text",
    });

    if (json.success) {
      const serverMsg = {
        ...json.data,
        _status: normalizeStatus(json.data),
      };

      setMessages((prev) =>
        prev.map((m) => (m.id === tempId ? serverMsg : m))
      );
    }

    loadMessages();
  };

  // --------------------------------------
  // IMAGE UPLOAD
  // --------------------------------------
  const handleImageButton = () => fileInputRef.current?.click();

  const handleImageSelected = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const filePath = `images/${matchId}/${Date.now()}-${file.name}`;

    const { error } = await supabase.storage.from("chat-media").upload(filePath, file);
    if (error) {
      alert("Image upload error");
      return;
    }

    const { data } = supabase.storage.from("chat-media").getPublicUrl(filePath);
    const url = data.publicUrl;

    const json = await sendMessageToBackend({
      match_id: matchId,
      sender_id: currentUserId,
      receiver_id: otherUserId,
      type: "image",
      image_url: url,
    });

    if (json.success) {
      setMessages((prev) => [...prev, json.data]);
      scrollToBottom();
    }

    loadMessages();
  };

  // --------------------------------------
  // AUDIO RECORD
  // --------------------------------------
  const toggleRecording = async () => {
    if (isRecording) {
      mediaRecorderRef.current?.stop();
      setIsRecording(false);
      return;
    }

    if (!navigator.mediaDevices) {
      alert("Microphone not supported");
      return;
    }

    const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
    const recorder = new MediaRecorder(stream);

    audioChunksRef.current = [];
    recorder.ondataavailable = (e) => audioChunksRef.current.push(e.data);

    recorder.onstop = async () => {
      const audioBlob = new Blob(audioChunksRef.current, { type: "audio/webm" });
      const audioPath = `audio/${matchId}/${Date.now()}-${currentUserId}.webm`;

      await supabase.storage.from("chat-media").upload(audioPath, audioBlob);
      const { data } = supabase.storage.from("chat-media").getPublicUrl(audioPath);

      const json = await sendMessageToBackend({
        match_id: matchId,
        sender_id: currentUserId,
        receiver_id: otherUserId,
        type: "audio",
        audio_url: data.publicUrl,
      });

      if (json.success) {
        setMessages((prev) => [...prev, json.data]);
        scrollToBottom();
      }

      loadMessages();
    };

    mediaRecorderRef.current = recorder;
    recorder.start();
    setIsRecording(true);
  };

  // --------------------------------------
  // STATUS ICON RENDERER
  // --------------------------------------
  const statusIcon = (s: MessageStatus) =>
    s === "sent" ? (
      <Check className="w-3 h-3" />
    ) : s === "delivered" ? (
      <CheckCheck className="w-3 h-3" />
    ) : (
      <CheckCheck className="w-3 h-3 text-blue-500" />
    );

  // --------------------------------------
  // IMAGE + AUDIO RENDER
  // --------------------------------------
  const renderContent = (msg: any) => {
    if (msg.type === "image" && msg.image_url) {
      return <img src={msg.image_url} className="max-w-full rounded-xl mb-1" />;
    }

    if (msg.type === "audio" && msg.audio_url) {
      return (
        <audio controls className="w-40">
          <source src={msg.audio_url} />
        </audio>
      );
    }

    return <p>{msg.message}</p>;
  };

  return (
    <div className="h-screen flex flex-col bg-gray-100">

      {/* HEADER */}
      <div className="p-4 bg-deep-blue text-white flex items-center space-x-3 shadow">
        <div className="w-10 h-10 rounded-full bg-white/20 flex items-center justify-center">
          
        </div>
        <div className="flex-1">
          <div className="font-gujarati font-semibold"></div>
          <div className="flex items-center text-xs space-x-1">
            <span className={`w-2 h-2 rounded-full ${isOnline ? "bg-green-400" : "bg-gray-400"}`}></span>
            <span>{isOnline ? "Online" : "Offline"}</span>
          </div>
        </div>
      </div>

      {/* CHAT WINDOW */}
      <div className="flex-1 overflow-y-auto p-4 space-y-3">

        {messages.map((msg, i) => {
          const mine = msg.sender_id === currentUserId;
          const st = msg._status as MessageStatus;

          return (
            <div
              key={msg.id || i}
              className={`max-w-[75%] px-4 py-2 rounded-2xl shadow ${
                mine
                  ? "bg-blue-600 text-white ml-auto rounded-br-sm"
                  : "bg-white text-gray-900 mr-auto rounded-bl-sm"
              }`}
            >
              {renderContent(msg)}

              {/* time + ticks */}
              <div className="flex items-center justify-end space-x-1 text-[10px] mt-1">
                <span>
                  {new Date(msg.created_at).toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </span>
                {mine && statusIcon(st)}
              </div>
            </div>
          );
        })}

        {/* TYPING INDICATOR */}
        {otherTyping && (
          <div className="bg-white px-3 py-2 rounded-2xl shadow w-max mr-auto flex items-center space-x-2">
            <CircleDot className="w-3 h-3 text-green-500 animate-pulse" />
            <span className="text-xs font-gujarati">   </span>
          </div>
        )}

        <div ref={bottomRef}></div>
      </div>

      {/* INPUT BAR */}
      <div className="p-3 bg-white border-t flex items-center space-x-2 shadow">

        {/* HIDDEN IMAGE INPUT */}
        <input
          type="file"
          ref={fileInputRef}
          className="hidden"
          onChange={handleImageSelected}
          accept="image/*"
        />

        {/* IMAGE BUTTON */}
        <button
          onClick={handleImageButton}
          className="p-2 rounded-full bg-gray-200"
        >
          <ImageIcon className="w-5 h-5 text-gray-700" />
        </button>

        {/* MIC BUTTON */}
        <button
          onClick={toggleRecording}
          className={`p-2 rounded-full ${
            isRecording ? "bg-red-500 text-white" : "bg-gray-200 text-gray-700"
          }`}
        >
          <Mic className="w-5 h-5" />
        </button>

        {/* INPUT */}
        <input
          value={text}
          onChange={(e) => handleInputChange(e.target.value)}
          placeholder=" "
          className="flex-1 px-4 py-2 border rounded-full"
        />

        {/* SEND BUTTON */}
        <button
          onClick={sendTextMessage}
          className="p-2 bg-blue-600 rounded-full text-white"
        >
          <Send className="w-5 h-5" />
        </button>
      </div>
    </div>
  );
}
