import { useState } from "react";
import { useSearchParams, useNavigate } from "react-router-dom";

export default function RegisterDetailsScreen() {
  const [params] = useSearchParams();
  const email = params.get("email");

  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const registerUser = async () => {
    if (!phone.trim() || !password.trim() || !confirm.trim()) {
      alert("  .");
      return;
    }

    if (password !== confirm) {
      alert("    ");
      return;
    }

    setLoading(true);

    const res = await fetch("http://localhost:4000/register-user", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, phone, password }),
    });

    const json = await res.json();
    setLoading(false);

    if (json.success) {
      alert("   ");
      navigate("/login");
    } else {
      alert("     ");
    }
  };

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-xl font-gujarati font-bold"> </h1>

      <input
        type="tel"
        placeholder=" "
        value={phone}
        onChange={(e) => setPhone(e.target.value)}
        className="w-full p-3 border rounded-xl"
      />

      <input
        type="password"
        placeholder=" "
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        className="w-full p-3 border rounded-xl"
      />

      <input
        type="password"
        placeholder="  "
        value={confirm}
        onChange={(e) => setConfirm(e.target.value)}
        className="w-full p-3 border rounded-xl"
      />

      <button
        onClick={registerUser}
        disabled={loading}
        className="w-full bg-deep-blue text-white p-3 rounded-xl font-gujarati"
      >
        {loading ? "   " : " "}
      </button>
    </div>
  );
}
