import { useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { supabase } from "../supabaseClient";

export default function RegisterVerifyScreen() {
  const [otp, setOtp] = useState("");
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [params] = useSearchParams();
  const email = params.get("email");

  const verify = async () => {
    if (!otp.trim()) return alert("OTP  .");

    setLoading(true);

    const { error } = await supabase.auth.verifyOtp({
      email,
      token: otp,
      type: "email",
    });

    setLoading(false);

    if (error) {
      alert("OTP   ");
    } else {
      alert("  ");
      navigate(`/register-details?email=${email}`);
    }
  };

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-xl font-gujarati font-bold">OTP  </h1>

      <input
        type="text"
        placeholder="OTP  "
        value={otp}
        onChange={(e) => setOtp(e.target.value)}
        className="w-full p-3 border rounded-xl"
      />

      <button
        onClick={verify}
        disabled={loading}
        className="w-full bg-green-600 text-white p-3 rounded-xl font-gujarati"
      >
        {loading ? "   " : " "}
      </button>
    </div>
  );
}
