import { useState, useEffect, useMemo } from "react";
import { Search, Heart } from "lucide-react";
import { motion } from "framer-motion";
import BottomNav from "../components/BottomNav";
import { useNavigate } from "react-router-dom";

const API_URL = "http://localhost:4000";

// CURRENT LOGGED-IN USER (from LoginScreen)
function getCurrentUserId() {
  try {
    const raw = localStorage.getItem("currentUser");
    if (!raw) return null;
    const user = JSON.parse(raw);
    return user.id || null; // phone used as user id
  } catch {
    return null;
  }
}

const currentUserId = getCurrentUserId();

type TabType = "list" | "detail" | "myprofile";

interface ProfileRow {
  id: string;
  phone: string | null;
  full_name: string | null;
  father_name?: string | null;
  mother_name?: string | null;
  sub_surname?: string | null;
  mother_sub_surname?: string | null;
  gol?: string | null;
  age?: number | null;
  city?: string | null;
  taluka?: string | null;
  district?: string | null;
  education?: string | null;
  occupation?: string | null;
  kundali_available?: boolean | null;
  birth_date?: string | null;
  gender?: string | null;
  avatar_url?: string | null;
}

// PROFILE COMPLETE CHECK
function isProfileComplete(p: ProfileRow | null | undefined) {
  if (!p) return false;

  return (
    !!p.full_name &&
    !!p.father_name &&
    !!p.mother_name &&
    !!p.sub_surname &&
    !!p.mother_sub_surname &&
    !!p.gol &&
    p.age != null &&
    !!p.city &&
    !!p.taluka &&
    !!p.district &&
    !!p.education &&
    !!p.occupation &&
    !!p.birth_date &&
    !!p.gender
  );
}

export default function MatrimonyScreen() {
  const navigate = useNavigate();

  const [profiles, setProfiles] = useState<ProfileRow[]>([]);
  const [loading, setLoading] = useState(true);

  const [activeTab, setActiveTab] = useState<TabType>("list");
  const [selectedProfile, setSelectedProfile] = useState<ProfileRow | null>(
    null
  );

  // CHAT UNLOCK STATE (will use later when match flow ready)
  const [canChat, setCanChat] = useState(false);
  const [matchId, setMatchId] = useState<string | null>(null);

  // MY PROFILE FORM STATE
  const [fullName, setFullName] = useState("");
  const [fatherName, setFatherName] = useState("");
  const [motherName, setMotherName] = useState("");
  const [subSurname, setSubSurname] = useState("");
  const [motherSubSurname, setMotherSubSurname] = useState("");
  const [gol, setGol] = useState("");
  const [age, setAge] = useState("");
  const [village, setVillage] = useState("");
  const [taluka, setTaluka] = useState("");
  const [district, setDistrict] = useState("");
  const [education, setEducation] = useState("");
  const [occupation, setOccupation] = useState("");
  const [birthDate, setBirthDate] = useState("");
  const [gender, setGender] = useState<"" | "">("");
  const [kundali, setKundali] = useState(false);
  const [savingProfile, setSavingProfile] = useState(false);

  // LOAD PROFILES FROM BACKEND
  const loadProfiles = async () => {
    try {
      setLoading(true);
      const res = await fetch(`${API_URL}/profiles`);
      const json = await res.json();
      const rows = (json.data || []) as ProfileRow[];
      setProfiles(rows);
    } catch (err) {
      console.log("PROFILES FETCH ERROR:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadProfiles();
  }, []);

  // FIND MY PROFILE FROM LIST (BY PHONE)
  const myProfile: ProfileRow | null = useMemo(() => {
    if (!currentUserId) return null;
    return profiles.find((p) => p.phone === currentUserId) || null;
  }, [profiles]);

  const myProfileComplete = useMemo(
    () => isProfileComplete(myProfile),
    [myProfile]
  );

  // PREFILL MY PROFILE FORM WHEN DATA AVAILABLE
  useEffect(() => {
    if (!myProfile || !currentUserId) return;

    setFullName(myProfile.full_name || "");
    setFatherName(myProfile.father_name || "");
    setMotherName(myProfile.mother_name || "");
    setSubSurname(myProfile.sub_surname || "");
    setMotherSubSurname(myProfile.mother_sub_surname || "");
    setGol(myProfile.gol || "");
    setAge(myProfile.age != null ? String(myProfile.age) : "");
    setVillage(myProfile.city || "");
    setTaluka(myProfile.taluka || "");
    setDistrict(myProfile.district || "");
    setEducation(myProfile.education || "");
    setOccupation(myProfile.occupation || "");
    setBirthDate(
      myProfile.birth_date
        ? myProfile.birth_date.slice(0, 10) // YYYY-MM-DD
        : ""
    );
    setGender(
      myProfile.gender === ""
        ? ""
        : "" // default male
    );
    setKundali(!!myProfile.kundali_available);
  }, [myProfile]);

  // PROFILE LIST ONLY SHOW COMPLETE PROFILES
  const completeProfiles = useMemo(
    () => profiles.filter((p) => isProfileComplete(p)),
    [profiles]
  );

  // DEFAULT SELECTED PROFILE
  useEffect(() => {
    if (!selectedProfile && completeProfiles.length > 0) {
      setSelectedProfile(completeProfiles[0]);
    }
  }, [completeProfiles, selectedProfile]);

  // AUTO CHECK MATCH WHEN SELECTED PROFILE CHANGES
  useEffect(() => {
    const checkMatch = async () => {
      if (!selectedProfile || !currentUserId) return;

      try {
        // IMPORTANT: we use phone as user id
        const otherId = selectedProfile.phone;
        if (!otherId) return;

        const res = await fetch(
          `${API_URL}/check-match?user1=${currentUserId}&user2=${otherId}`
        );
        const json = await res.json();

        if (json.matched) {
          setCanChat(true);
          setMatchId(json.match_id);
        } else {
          setCanChat(false);
          setMatchId(null);
        }
      } catch (err) {
        console.log("CHECK MATCH ERROR:", err);
      }
    };

    checkMatch();
  }, [selectedProfile]);

  // SAVE / UPDATE MY PROFILE  /profiles
  const handleSaveMyProfile = async () => {
    if (!currentUserId) {
      alert("Login  .");
      return;
    }

    // basic front-end validation
    if (
      !fullName ||
      !fatherName ||
      !motherName ||
      !subSurname ||
      !motherSubSurname ||
      !gol ||
      !age ||
      !village ||
      !taluka ||
      !district ||
      !education ||
      !occupation ||
      !birthDate ||
      !gender
    ) {
      alert("    .");
      return;
    }

    try {
      setSavingProfile(true);

      const payload = {
        phone: currentUserId,
        full_name: fullName,
        father_name: fatherName,
        mother_name: motherName,
        sub_surname: subSurname,
        mother_sub_surname: motherSubSurname,
        gol,
        age: Number(age),
        city: village,
        taluka,
        district,
        education,
        occupation,
        birth_date: birthDate,
        gender,
        kundali_available: kundali,
      };

      const res = await fetch(`${API_URL}/profiles`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const json = await res.json();

      if (!json.success) {
        console.log("PROFILE SAVE ERROR:", json);
        alert("     .");
        return;
      }

      alert("     ");
      await loadProfiles();
    } catch (err) {
      console.log("PROFILE SAVE ERROR:", err);
      alert("   ,   .");
    } finally {
      setSavingProfile(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center text-xl font-gujarati">
           ...
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 pb-24">
      {/* HEADER */}
      <div className="bg-gradient-to-r from-pink-500 to-rose-500 px-6 py-6">
        <h1 className="text-white font-gujarati font-bold text-2xl">
          
        </h1>
        <p className="text-white/80 text-sm font-gujarati">
            
        </p>
      </div>

      {/* TABS */}
      <div className="bg-white border-b px-6 pt-4 sticky top-0 z-10">
        <div className="flex space-x-4">
          <button
            onClick={() => setActiveTab("list")}
            className={`pb-3 font-gujarati ${
              activeTab === "list"
                ? "text-deep-blue border-b-2 border-deep-blue"
                : "text-gray-500"
            }`}
          >
             
          </button>

          <button
            onClick={() => setActiveTab("detail")}
            className={`pb-3 font-gujarati ${
              activeTab === "detail"
                ? "text-deep-blue border-b-2 border-deep-blue"
                : "text-gray-500"
            }`}
            disabled={!selectedProfile}
          >
             
          </button>

          <button
            onClick={() => setActiveTab("myprofile")}
            className={`pb-3 font-gujarati ${
              activeTab === "myprofile"
                ? "text-deep-blue border-b-2 border-deep-blue"
                : "text-gray-500"
            }`}
          >
             
          </button>
        </div>
      </div>

      {/* CONTENT */}
      <div className="px-6 py-6 space-y-6">
        {/* SMALL INFO BANNER */}
        {!myProfileComplete && (
          <div className="bg-amber-50 border border-amber-200 text-amber-800 text-sm font-gujarati px-4 py-3 rounded-xl">
                      {" "}
            <b>     .</b>
          </div>
        )}

        {/* LIST TAB */}
        {activeTab === "list" && (
          <div className="space-y-4">
            {/* SEARCH UI -   UI, filter     add    */}
            <div className="flex space-x-3">
              <div className="flex-1 relative">
                <Search className="absolute left-4 top-3 text-gray-400" />
                <input
                  className="w-full pl-12 pr-4 py-3 border rounded-xl font-gujarati"
                  placeholder="..."
                />
              </div>
            </div>

            {completeProfiles.length === 0 && (
              <div className="text-center text-gray-500 font-gujarati mt-10">
                      .
              </div>
            )}

            {completeProfiles.map((p, i) => (
              <motion.div
                key={p.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: i * 0.06 }}
                className="premium-card p-4"
              >
                <div className="flex space-x-4">
                  <img
                    src={p.avatar_url || "https://via.placeholder.com/120"}
                    className="w-24 h-24 rounded-xl object-cover"
                  />
                  <div className="flex-1">
                    <h3 className="font-gujarati font-bold">
                      {p.full_name || " "}
                    </h3>
                    <p className="font-gujarati text-sm">
                      : {p.age || "-"}
                    </p>
                    <p className="font-gujarati text-sm">
                      : {p.city || "-"}
                    </p>
                    {p.gol && (
                      <p className="font-gujarati text-xs text-gray-600 mt-1">
                        : {p.gol}
                      </p>
                    )}
                  </div>
                </div>

                <div className="flex space-x-3 mt-3">
                  <button
                    onClick={() => {
                      setSelectedProfile(p);
                      setActiveTab("detail");
                    }}
                    className="flex-1 bg-deep-blue text-white py-2 rounded-xl font-gujarati"
                  >
                     
                  </button>
                  <button className="w-12 h-12 bg-pink-50 rounded-xl flex items-center justify-center">
                    <Heart className="w-5 h-5 text-pink-500" />
                  </button>
                </div>
              </motion.div>
            ))}
          </div>
        )}

        {/* DETAIL TAB */}
        {activeTab === "detail" && selectedProfile && (
          <motion.div
            key={selectedProfile.id}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="space-y-6"
          >
            <img
              src={
                selectedProfile.avatar_url ||
                "https://via.placeholder.com/400x300"
              }
              className="w-full h-80 rounded-xl object-cover"
            />

            <div className="premium-card p-6 space-y-2">
              <h2 className="text-xl font-gujarati font-bold">
                 
              </h2>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.full_name}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.father_name}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.mother_name}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.age}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.city}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.gol}
              </p>
              <p className="font-gujarati">
                <b>:</b> {selectedProfile.education}
              </p>
              <p className="font-gujarati">
                <b>/:</b> {selectedProfile.occupation}
              </p>

              {selectedProfile.kundali_available && (
                <p className="font-gujarati text-green-600 flex items-center space-x-1">
                  <CheckCircle className="w-4 h-4 mr-1" />
                  <span> </span>
                </p>
              )}
            </div>

            {/* SEND REQUEST */}
            <button
              onClick={async () => {
                if (!currentUserId) {
                  alert("Login  .");
                  return;
                }

                if (!myProfileComplete) {
                  alert(
                    "       ."
                  );
                  setActiveTab("myprofile");
                  return;
                }

                const otherId = selectedProfile.phone;
                if (!otherId) {
                  alert("     .");
                  return;
                }

                try {
                  await fetch(`${API_URL}/send-request`, {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({
                      from_user_id: currentUserId,
                      to_user_id: otherId,
                    }),
                  });

                  alert("   ");
                } catch (err) {
                  console.log("SEND REQUEST ERROR:", err);
                  alert("  .   .");
                }
              }}
              className="w-full bg-mint text-deep-blue font-semibold py-3 rounded-xl font-gujarati"
            >
               
            </button>

            {/* CHAT BUTTON ( match ready ) */}
            {canChat && matchId && (
              <button
                onClick={() =>
                  navigate(
                    `/messages?matchId=${matchId}&other=${selectedProfile.phone}`
                  )
                }
                className="w-full bg-green-600 text-white py-3 rounded-xl font-gujarati font-semibold"
              >
                  
              </button>
            )}
          </motion.div>
        )}

        {/* MYPROFILE TAB */}
        {activeTab === "myprofile" && (
          <motion.div
            key="myprofile"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="space-y-4"
          >
            <h2 className="text-lg font-gujarati font-bold text-gray-800 text-center">
                
            </h2>

            {!currentUserId && (
              <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-xl font-gujarati text-sm">
                    Login .
              </div>
            )}

            {/* ALL FIELDS */}
            {[
              { label: "", value: fullName, setter: setFullName },
              { label: "  ", value: fatherName, setter: setFatherName },
              { label: "  ", value: motherName, setter: setMotherName },
              { label: " ", value: subSurname, setter: setSubSurname },
              {
                label: "  ",
                value: motherSubSurname,
                setter: setMotherSubSurname,
              },
              { label: " (Sub-community)", value: gol, setter: setGol },
              { label: "", value: age, setter: setAge },
              { label: "", value: village, setter: setVillage },
              { label: "", value: taluka, setter: setTaluka },
              { label: "", value: district, setter: setDistrict },
              { label: "", value: education, setter: setEducation },
              { label: "/", value: occupation, setter: setOccupation },
            ].map((f, idx) => (
              <div key={idx}>
                <label className="text-sm font-gujarati text-gray-600 mb-1 block">
                  {f.label}
                </label>
                <input
                  type="text"
                  className="w-full px-4 py-2 border border-gray-300 rounded-xl focus:ring-mint focus:outline-none font-gujarati"
                  placeholder={`${f.label}  `}
                  value={f.value}
                  onChange={(e) => f.setter(e.target.value)}
                />
              </div>
            ))}

            {/*   */}
            <div>
              <label className="text-sm font-gujarati text-gray-600 mb-1 block">
                 
              </label>
              <input
                type="date"
                className="w-full px-4 py-2 border border-gray-300 rounded-xl font-gujarati"
                value={birthDate}
                onChange={(e) => setBirthDate(e.target.value)}
              />
            </div>

            {/*  */}
            <div>
              <label className="text-sm font-gujarati text-gray-600 mb-1 block">
                
              </label>
              <div className="flex items-center space-x-4">
                <label className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name="gender"
                    value=""
                    checked={gender === ""}
                    onChange={() => setGender("")}
                  />
                  <span className="font-gujarati"></span>
                </label>
                <label className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name="gender"
                    value=""
                    checked={gender === ""}
                    onChange={() => setGender("")}
                  />
                  <span className="font-gujarati"></span>
                </label>
              </div>
            </div>

            {/*  */}
            <div className="flex items-center justify-between mt-2">
              <span className="font-gujarati text-sm text-gray-600">
                 ?
              </span>
              <input
                type="checkbox"
                checked={kundali}
                onChange={(e) => setKundali(e.target.checked)}
              />
            </div>

            <button
              onClick={handleSaveMyProfile}
              disabled={savingProfile || !currentUserId}
              className="w-full bg-deep-blue text-white py-3 rounded-xl font-gujarati mt-4 disabled:bg-gray-400"
            >
              {savingProfile
                ? "   ..."
                : "    "}
            </button>
          </motion.div>
        )}
      </div>

      <BottomNav />
    </div>
  );
}
