import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { useNavigate, Link } from "react-router-dom";
import { Fingerprint, Smartphone } from "lucide-react";

export default function LoginScreen() {
  const [activeTab, setActiveTab] = useState<"login" | "register">("login");
  const navigate = useNavigate();

  // LOGIN FORM STATE
  const [loginPhone, setLoginPhone] = useState("");
  const [loginPassword, setLoginPassword] = useState("");

  // REGISTER FORM STATE
  const [regName, setRegName] = useState("");
  const [regPhone, setRegPhone] = useState("");
  const [regDOB, setRegDOB] = useState("");
  const [regPassword, setRegPassword] = useState("");
  const [regConfirm, setRegConfirm] = useState("");

  // ALREADY LOGGED-IN  DIRECT HOME
  useEffect(() => {
    const saved = localStorage.getItem("currentUser");
    if (saved) {
      navigate("/home", { replace: true });
    }
  }, [navigate]);

  // -------------------------
  // LOGIN LOGIC
  // -------------------------
  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();

    const phone = loginPhone.trim();
    const pass = loginPassword.trim();

    if (!phone || !pass) {
      alert("   .");
      return;
    }

    const saved = localStorage.getItem("appUsers");
    if (!saved) {
      alert("  ,   .");
      return;
    }

    const users = JSON.parse(saved);
    const found = users.find(
      (u: any) => u.phone === phone && u.password === pass
    );

    if (!found) {
      alert("    .");
      return;
    }

    // LOGIN SUCCESS
    localStorage.setItem("currentUser", JSON.stringify(found));
    navigate("/home", { replace: true });
  };

  // -------------------------
  // REGISTER LOGIC
  // -------------------------
  const handleRegister = (e: React.FormEvent) => {
    e.preventDefault();

    const name = regName.trim();
    const phone = regPhone.trim();
    const dob = regDOB.trim();
    const pass = regPassword.trim();
    const confirm = regConfirm.trim();

    if (!name || !phone || !dob || !pass || !confirm) {
      alert("   .");
      return;
    }

    if (pass !== confirm) {
      alert("     .");
      return;
    }

    let existing = localStorage.getItem("appUsers");
    let users = existing ? JSON.parse(existing) : [];

    if (users.some((u: any) => u.phone === phone)) {
      alert("      .");
      return;
    }

    const newUser = {
      id: phone, // same id used in matrimony + chat
      name,
      phone,
      dob,
      password: pass,
    };

    users.push(newUser);

    localStorage.setItem("appUsers", JSON.stringify(users));
    localStorage.setItem("currentUser", JSON.stringify(newUser));

    alert("   !");
    navigate("/home", { replace: true });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-deep-blue via-[#1A8FA3] to-mint flex flex-col safe-area-top safe-area-bottom">
      {/* Header */}
      <div className="text-center pt-12 pb-8 px-6">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-3xl font-bold text-white font-gujarati mb-2"
        >
            
        </motion.h1>
        <p className="text-mint text-sm"> </p>
      </div>

      {/* Tabs */}
      <div className="flex space-x-2 px-6 mb-6">
        <button
          onClick={() => setActiveTab("login")}
          className={`flex-1 py-3 rounded-2xl font-gujarati font-medium transition-all ${
            activeTab === "login"
              ? "bg-white text-deep-blue shadow-lg"
              : "bg-white/20 text-white"
          }`}
        >
           
        </button>
        <button
          onClick={() => setActiveTab("register")}
          className={`flex-1 py-3 rounded-2xl font-gujarati font-medium transition-all ${
            activeTab === "register"
              ? "bg-white text-deep-blue shadow-lg"
              : "bg-white/20 text-white"
          }`}
        >
            
        </button>
      </div>

      {/* Card */}
      <div className="flex-1 bg-white rounded-t-[2rem] px-6 pt-8 pb-6 overflow-y-auto">
        {/* LOGIN FORM */}
        {activeTab === "login" ? (
          <motion.form
            key="login"
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            onSubmit={handleLogin}
            className="space-y-6"
          >
            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                 
              </label>
              <input
                type="tel"
                placeholder="10  "
                value={loginPhone}
                onChange={(e) => setLoginPhone(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                
              </label>
              <input
                type="password"
                placeholder="  "
                value={loginPassword}
                onChange={(e) => setLoginPassword(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <button
              type="submit"
              className="w-full bg-mint hover:bg-mint/90 text-deep-blue font-gujarati font-semibold py-4 rounded-2xl transition-all shadow-mint"
            >
               
            </button>

            <div className="space-y-3 text-center">
              <Link
                to="/forgot-password"
                className="text-deep-blue text-sm font-gujarati hover:underline"
              >
                  ?
              </Link>
            </div>

            {/* Biometric (UI only) */}
            <div className="pt-6 border-t border-gray-200">
              <p className="text-center text-gray-500 text-sm mb-4">
                Biometric Login
              </p>
              <div className="flex justify-center space-x-6">
                <button
                  type="button"
                  className="flex flex-col items-center space-y-2 group"
                >
                  <div className="w-16 h-16 rounded-full bg-mint/10 flex items-center justify-center group-hover:bg-mint/20 transition-all">
                    <Fingerprint className="w-8 h-8 text-deep-blue" />
                  </div>
                  <span className="text-xs text-gray-600">Touch ID</span>
                </button>
                <button
                  type="button"
                  className="flex flex-col items-center space-y-2 group"
                >
                  <div className="w-16 h-16 rounded-full bg-mint/10 flex items-center justify-center group-hover:bg-mint/20 transition-all">
                    <Smartphone className="w-8 h-8 text-deep-blue" />
                  </div>
                  <span className="text-xs text-gray-600">Face ID</span>
                </button>
              </div>
            </div>
          </motion.form>
        ) : (
          // REGISTER FORM
          <motion.form
            key="register"
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            onSubmit={handleRegister}
            className="space-y-5"
          >
            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                
              </label>
              <input
                type="text"
                placeholder=" "
                value={regName}
                onChange={(e) => setRegName(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                 
              </label>
              <input
                type="tel"
                placeholder="10  "
                value={regPhone}
                onChange={(e) => setRegPhone(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                 
              </label>
              <input
                type="date"
                value={regDOB}
                onChange={(e) => setRegDOB(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <div>
              <label className="block text-gray-700 font-gujarati mb-2 text-sm">
                
              </label>
              <input
                type="password"
                placeholder=" "
                value={regPassword}
                onChange={(e) => setRegPassword(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <div>
              <label className="block text-gray-700 font-gujar mb-2 text-sm">
                  
              </label>
              <input
                type="password"
                placeholder="   "
                value={regConfirm}
                onChange={(e) => setRegConfirm(e.target.value)}
                className="w-full px-4 py-3 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-mint font-gujarati"
              />
            </div>

            <button
              type="submit"
              className="w-full bg-deep-blue hover:bg-deep-blue/90 text-white font-gujarati font-semibold py-4 rounded-2xl transition-all shadow-lg"
            >
               
            </button>
          </motion.form>
        )}
      </div>
    </div>
  );
}
